
ul.logo {

    list-style: none;

    li {
        padding: 1.3rem;
        vertical-align: middle;
    }

    .logo-description {
        @extend .title;
        color: $grey !important;
        font-weight: 500;
        font-size: 2.5rem;
        font-family: $family-serif;
        font-weight: 500;
        font-style: italic;
        text-align: center;
    }
    
    .logo-image {
        text-align: center;
        margin-bottom: 0 !important; 
        color: $blue !important;
        font-family: $family-sans-serif;
        font-size: 4rem;
        text-shadow: 2px 2px 3px rgba(255,255,255,0.5);

        -webkit-background-clip: text;
        -moz-background-clip: text;
        background-clip: text;;    
    }
}

.logo-container {
    .hero & {
    }
}

.logo-image img {
    .hero & {
        height: 1.2em;  
    }

    height: 1em;
    width: auto;
    vertical-align: middle;
}

.navbar-brand .logo-image {
    color: $blue;
    font-family: $family-sans-serif;
    font-style: normal;
    img {
        max-height: inherit;
        height: 1.5em;
    }

    .logo-label {
        display: inline-block;
        vertical-align: middle;
        text-shadow: none;
    }
}