.highlights-container {
	background-color: white;

	.section-controls {
		margin-top: 2rem;
	}
}

.news-outline {
	@extend .is-unselectable;

	.news-outline-image {
		background-size: cover;
		background-repeat: no-repeat;
	}

	.news-date {
		opacity: 0.5;
		filter: alpha(opacity=50);
	}
}

#news_outline {
	.slider-slide {
		padding: 20px;
	}

	.slider-control-bottomcenter {
		bottom: -35px !important;
	}
}

.news-grid {

	&-item {
		min-height: 300px;
		position: relative;
		background: transparent;
		

		.news-image {
			overflow: hidden;
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;

			z-index: -1;
			
			img {
				min-width: 100%;
				min-height: 100%;
			}
		}

		.news-content {
			color: white;
			position: absolute;
			bottom: 0;
			right: 0;
			left: 0;

			padding: 1rem;

			h2 {
				text-shadow: none;
			}

			a {
				color: white;
				&:hover {
					color: darken(white, 12%);
				}
			}
		}

		.news-heading {
			color: white;
			font-size: $size-large;
		}

		.news-desc {
			max-height: 0px;
			transition-property: max-height;
			transition-duration: 1s;

			overflow: hidden;
		}

		&:hover .news-desc {
			max-height: 150px;
			transition-property: max-height;
			transition-duration: 1s;
		}
	}
}

.news-listing {

	&--item {
		border-top: 1px solid $grey-light;
		padding: 0.5rem 0;

		img {
			max-width: 75%;
			display:block;
			float: right;
		}
	}
}

.news-filter-btn {
	width: 150px;
}