h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: bold;	
	text-shadow: 2px 2px 3px rgba(255,255,255,0.5);
}

p.content {
	text-align: justify;
}

.section-heading {
	@extend .title;
	@extend .is-3;
	text-align: center;
	font-weight: 500 !important;
	text-shadow: 4px 4px 4px $grey-light;

	&:after {
		content: ' ';
		display: block;
		width: 40px;
		margin: 0 auto;

		margin-top: .5rem;

		border-bottom: 3px solid $green;
	}


	&.margin-top {
		margin-top: 2rem;
		
		&-2x {
			margin-top: 3rem;
		}
	}

	&.margin-bottom {
		margin-bottom: 1rem;	

		&-2x {
			margin-bottom: 2rem;
		}

		&-3x {
			margin-bottom: 3rem;
		}
	}
}