$team-member-border-color: darken($grey-light, 6%);

.team-outline-container {
	background-color: $grey-light;
}

.team-member {
	background-color: white;
	padding: 1rem;
	border: 1px solid $team-member-border-color;
	box-shadow: 10px 5px 21px -8px rgba(#333333, 0.4);

	.team-listing & {
		box-shadow: none;
		background-color: transparent;
		border: none;

		.team-member-image {
			margin: auto;
		}
	}

	&-ghost {
		border: 1px solid $team-member-border-color;
		border: 1px solid $team-member-border-color;
		background-color: white;
		font-size: 1.2rem;
		font-weight: 500;
		text-align: center;
		min-height: 250px;
		position: relative;
	}

	&-see-more {
		display: block;
		width: 100%;
		text-align: center;
		position: absolute;
		top: 50%;
		padding: 1rem;
		transform: translateY(-59%);
	}

	.team-member-image {
		margin: 0 auto;
		margin-top: -50px;
		background-color: white;
		padding: 0.5rem;
		border: 1px solid $team-member-border-color;
		border-radius: $radius-rounded;
		width: 260px;
		height: 260px;
	}

	.team-member-name {
		text-align: center;
		margin-top: 1rem;
		font-size: 1.1rem;
		margin-bottom: 0;
	}

	.team-member-title {
		margin-bottom: 1rem;
		color: $grey;
		font-style: italic;
		text-align: center
	}

	.team-member-description {
		text-align: justify;
		height: (1em * 1.5) * 8;
		overflow: hidden;
	}
}

// team page
.team-listing {
	$border: darken($grey-light, 10%);

	min-height: 100vh;

	&-member {
		background-color: $grey-light;
		border-top: 1px solid $border;
		padding: 3rem 0;


		&-container {
			border-right: 1px solid $border;
		}
	}

	&-detail {
		padding: 2rem;
	}

	&-para-heading {
		padding-bottom: 1em * 1.5;
	}

	&-para {
		padding-bottom: 1em * 1.5;
	}

	.team-member-contact {

		.content {
			text-align: center;
		}
	}
}
